<template>
  <v-parallax
    class="login"
    dark
    :src="require('@/assets/hero2.jpg')"
    height="100%"
  >
    <v-container fluid fill-height>
      <v-layout align-center justify-center>
        <v-flex xs12 sm8 md4>
            <v-card class="elevation-6">
              <v-toolbar dark color="primary">
                <v-toolbar-title>Login</v-toolbar-title>
              </v-toolbar>
              <v-card-text>
                <v-form 
                  v-model="valid"
                  ref="form"
                  @keyup.native.enter="submit"
                >
                  <v-text-field 
                    v-model="email"
                    prepend-icon="person"
                    name="login"
                    label="Login"
                    type="text"
                    :rules="[rules.required, rules.email]"
                  />
                  <v-text-field
                    v-model="password"
                    id="password"
                    prepend-icon="lock"
                    name="password"
                    label="Password"
                    :type="show ? 'text' : 'password'"
                    :append-icon="show ? 'visibility_off' : 'visibility'"
                    @click:append="show = !show"
                    :rules="[rules.required]"
                  />
                </v-form>
              </v-card-text>
              <v-card-actions>
                <span class="ml-2" style="color: red;">{{message}}</span>
                <v-spacer></v-spacer>
                <v-btn color="primary" @click="submit" :loading="loading" :disabled="!valid">Login</v-btn>
              </v-card-actions>
            </v-card>
        </v-flex>
      </v-layout>
    </v-container>
  </v-parallax>
</template>

<script>
import { EventBus } from '@/plugins/event-bus.js';

export default {
  data() {
    return {
      show: false,
      email: "",
      password: "",
      valid: false,
      loading: false,
      message: "",
      // Static data
      rules: {
        required: value => !!value || 'Required.',
        email: value => {
          const pattern = /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/
          return pattern.test(value) || 'Invalid e-mail.'
        }
      }
    }
  },
  methods: {
    submit() {
      this.loading = true
      if (this.$refs.form.validate()) {
        this.$Parse.User.logIn(this.email, this.password).then(user => {
          console.log(user)
          this.loading = false
          EventBus.$emit('userUpdate');
          this.$router.push('dashboard')
          EventBus.$emit('snackbar', "Logged in successfully.");
        }, (error) => {
          this.loading = false
          this.message = error.message
          console.error(error)
        })
      } else {
        this.loading = false
      }
    }
  }
}
</script>

<style scoped>
.login {
  height: 100%;
}
</style>